@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Need a system for assigning z-index 
(Determines the order that elements which overlap eachother are displayed on the page )*/
/* 
Z-index (higher index -> element displayed over other elements
Default Z-index (when not assigned) = 0

    My Z-index system: 
    10  -> Highest priority, 
     0  -> Default priority, 
    -1  -> Underneath all other elements.
 */

html {
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
  overflow-x: hidden;
  overflow-y: scroll;
}
.section-containers {
  height: 100vh;
  scroll-snap-align: start;
}

body {
  margin: 0;
  color: #efeeed;
}

.App {
  height: 300vh;
  overflow: hidden;
  background-color: rgb(55 55 55);
}
.top-content {
  height: 15%;
}
.title {
  display: inline;
  font-size: 14px;
  margin: 0;
  text-align: left;
  padding-left: 4px;
}
#addition {
  visibility: hidden;
  font-size: 1px;
  transition: font-size 0.5s;
}

.react-logo {
  margin-top: 5px;
  height: 45px;
  pointer-events: none;
}
.section-bar {
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0%;
  top: 15%;
  min-width: 250px;
  height: 300px;
}
.work.right {
  position: absolute;
  right: 0;
  height: 100%;
  width: 40%;
  z-index: 1;
}

.listless-list {
  list-style-type: none;
  font-size: 18px;
}
li>a {
  display: inline-block;
  margin: 10px;
  padding: 5px, 5px;
  font-family: 'Roboto', sans-serif;
  text-align: left;
  color: #efeeed;
  text-decoration: none;
}


#main-stuff-wrapper {
  text-align: center;
}


/* gradient Styling of the sections */

/*   opacity: 0.9; */
/*  background: linear-gradient(to bottom, , 99%, rgb(22, 64, 118, 0)); */
/*   background: linear-gradient(to bottom, rgb(49, 47, 47), 99.5%, rgb(29, 194, 7, 0));*/
/* background: #164076; */
#work {
  background: rgb(22, 64, 118);
}
#life {
  background: #f2b635;
}

.modal {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  max-width: 100%;
  height: 400px;
  max-height: 100%;
  /* highest z-index when displayed.*/
  z-index: 10;
}
.modal.closed {
 display: none;
}
.modal-guts {
  
  width: 100%;
  height: 90%;
  /* spacing around the edges of the modal box */
  padding: 20px;
  margin: 1%;
  /* let it scroll if message overflows the screen. */
}


.modal-overlay {
  /* 2nd highest z-index */
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.9;
  background-color: #222;
}
.modal-overlay.closed {
  display: none;
}

.bottom-content {
  height: 70%;
}
.myPics {
  height: 150px;
  max-height: 30%;
}
.contactMe {
  color: #efeeed;
  font-size: 22px;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
}
.social-links {
  display: inline;
  color: #efeeed;
}
.section-headings {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  margin: 0px;
  text-align: left;
}
#the-main-man {
  font-size: 40px;
}
.work.title {
  margin-top: 3em;
  display: block;
  text-align: center;
}
.work.list {
  list-style-type: none;
  text-align: left;
  font-size: small;
}
.based-titles {
  position: relative;
  left: -15%;
  color: #efeeed;
  font-family: 'Roboto', sans-serif;
  display: flex;
  width: 190px;
  min-width: -webkit-max-content;
  min-width: -moz-max-content;
  min-width: max-content;
  flex-direction: column;
  text-align: left;
  margin: 2% auto;
}


#funky-footer {
  position: fixed;
  bottom: 1%;
  left: 45%
}

.normal-header {
  font-size: 40px;
  position: fixed;
  left: 0;
  right: 0;
}
#positioner {
  display: flex;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0 auto;
}
.contact-form {
  width: 95%;
  height: 85%;
  padding: 2% 5%;
  background: #efeeed;
  border-radius: 10px;
  overflow: hidden;
}
.contact-form input, .contact-form textarea {
  margin: 5px 10px;
  border-radius: 5px;
  border: hidden;
}
.contact-form textarea {
  display: block;  
  margin: 0 auto;
  width: 100%;
  height: 100%;
  max-height: 50%;
  overflow: hidden;
}

@media screen and (max-width: 850px) {
  .based-titles {
    left: 0;
  }
  .optional-text {
    display: none;
  }
  .listless-list {
    font-size: 35px;
  }
  .contactMe {
    font-size: 35px;
  }
 
}
@media screen and (min-width: 860px) {
  .based-titles {
    left: -15%;
  }
  .optional-text {
    display: inline;
  }
  .listless-list {
    font-size: 18px;
  }
  .contactMe {
    font-size: 18px;
  }
}
/* default styles below */
@media (prefers-reduced-motion: no-preference) {
  .react-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
ul {
  -webkit-padding-start: 5px;
          padding-inline-start: 5px;
}
